@import "../../../helpers/Variables.scss";
@import "../../../helpers/Media-mixin.scss";

.button {
    width: 100%;
    height: 100%;
    border-radius: 80px;
    border: none;
    font-family: "Inter", sans-serif;
    font-size: $--font-primary;
    font-weight: $--weight-bold;
    color: $--0--white;
    background-color: $--6--yellow-secondary;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
}

._button-large {
    width: 200px;
    height: 60px;
}

._button-medium {
    width: 170px;
    height: 60px;
}

._button-small {
    width: 150px;
    height: 45px;
}

._button-small > * {
    font-size: $--font-small;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

._button-round-big {
    width: 60px;
    height: 60px;
    border-radius: 50%;
}

._button-round-small {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 10px;
    border-radius: 50%;
}
